.episode-details-container {
    flex: 1;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.episode-details-redo-icon{
    display: flex;
    margin-left: auto;
    margin-right: 40px;
    cursor: pointer;

    p {
        color: #8E6EB5;
        margin: auto 0 auto 5px;
        font-family: Helvetica;
        font-size: 14px;
    }
}

.episode-details-redo-icon:hover{
    p {
        font-weight: bold;
    }
}

.episode-details {
    display: flex;
}

.episode-details-section {
    display: flex;
    margin-top: 20px;
}

.episode-details-image {
    width: 500px;
    height: 400px;
    border-radius: 15px;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.68);
    margin: 0 40px;
}

.episode-details-image-error {
    width: 300px;
    height: 450px;
    border-radius: 15px;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.68);
    margin: 0 40px;

    p {
         margin: auto;
         font-size: 10px;
         color: white;
         line-height: 450px;
    }
}

.episode-details-text {
    color: #FFF;
    font-family: Helvetica;
    margin-right: 40px;
    text-align: start;
}

.episode-details-name {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 20px;
}

.episode-details-show-name {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 20px;
}

.episode-details-rating-section {
    display: flex;
    margin-bottom: 20px;
}

.episode-details-rating {
    color: #FFF;
    font-family: Helvetica;
    font-size: 16px;
    /* font-weight: 700; */
    margin: auto 0 auto 5px;
}

.episode-details-label {
    width: 150px
}