.generate-show-container {
    flex: 1;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.generate-show-info {
    margin: 40px auto;
    color: #8E6EB5;
    text-align: center;
    font-family: Helvetica;
    font-size: 20px;
}

.genre-button {
    width: 200px;
    height: 40px;
    border-radius: 15px;
    border: 2px dashed #8E6EB5;
    background: transparent;
    color: #FFF;
    text-align: center;
    font-family: Helvetica;
    font-size: 16px;
    font-weight: 400;
    cursor: pointer;
}

.genre-button.active {
    border: 2px solid #8E6EB5;
    background: #8E6EB5;
}

.genre-button:hover {
    border: 2px solid #8E6EB5;
}

.generate-show-button{
    margin-top: 40px;
}

.generate-show-button.no-error {
    margin-bottom: 40px;
}

.generate-show-error {
    color: #7F2F2F;
    margin-top: 10px;
    margin-bottom: 40px;
}